.startScreen{
    position: absolute;
    width: 100vw;
    height: 100vh;
    display: none;
    align-items: center;
    justify-content: center;
    background: #282c34;
    z-index: 33;
  }
  .startScreen.active{
    display: flex;
  }
  .startScreen div{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
  }
  .gameTitle{
    grid-column: span 2;
    text-align: center;
  }
  .gameSelect{
    color: white;
    background: #282c34;
    padding: 10px;
    border: white 1px solid;
    font-size: 24px;
    margin-bottom: 60px;
    text-align: center;
    cursor: pointer;
  }
  .gameSelect:active{
    background: white;
    color: black;
  }