.App{
  background-color: #282c34;
  color: white;
  font-style: italic;
  font-size: 18px;
  font-family: Arial, Helvetica, sans-serif;
  display: grid;
  grid-template-columns: 1fr 210px 1fr;
  align-items: center;

  position: relative;

}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
}
.wrappper{
  display: grid;
  grid-template-columns: repeat(3,70px);
  grid-template-rows: repeat(3, 70px);
  
  position: relative;

}
.wrapperItem{
  font-size: 50px;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  border: white 1px solid;
  user-select: none;
  cursor: pointer;
  z-index: 0;
}
.replay ,.info{
  color: white;
  background: #282c34;
  padding: 10px;
  border: white 1px solid;
  font-size: 24px;
  margin-bottom: 60px;
  text-align: center;
  cursor: pointer;

}
.replay:active  {
  background: white;
  color: black;
}

.gameOver-wrapper{
  display: flex;
  position: absolute;
  transform: translate(-10px,calc(-100vh - 210px));
  width: 210px;
  height: 210px;
  background: #282c34;
  border: white 1px solid;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  transition: 0.6s;
  padding: 10px;
}
.gameOver-wrapper.active{
  transition: 0.6s;
  transform: translate(-10px, -20px);
}
.gameOver-wrapper .replay{
  margin-bottom: 0;
}
.winner, .turn{
  font-size: 40px;
  font-weight: bold;
  align-items: center;
}
.gameover{
  background: white;
  color: #282c34;
  padding: 10px;
  display: flex;
  align-items: center;
  text-align: center;
  gap: 10px;
}
.cross{
  width: 80%;
  height: 2px;
  background: white;
  position: absolute;
  display: none;
  top: 105px;
  left:10%;
  transform: rotate(0);
  z-index: 100;
}

.cross.active{
  display: block;
}
.position0{
  top: 35px;
  left:10%;
}

.position1{
  top: 105px;
  left: -45px;
  rotate: 90deg;
}

.position2{
  width: 100%;
  top: 105px;
  left: 0%;
  transform: rotate(45deg)
}

.position3{
  top: 105px;
  left:10%;
}
.position4{
  top: 105px;
  left: 23px;
  rotate: 90deg;
}
.position5{
  width: 100%;
  top: 105px;
  left: 0;
  rotate: -45deg;
}
.position6{
  top: 176px;
  left:10%;
}
.position7{
  top: 105px;
  left: 91px;
  rotate: 90deg;
}
.win-x{
  background: green;
  color:white;
}
.win-o{
  background: red;
  color: white;
}





